
import Card from 'vux/src/components/card/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'

export default {
nmae: "user",
props: ["loginname"],
components: {
Card,
Cell,
Group
},
data: function() {
return {
userinfo:'',
collect_topics:''
};
},
beforeRouteEnter: function(to, from, next) {
next(function(vm) {

vm.$http.get("/user/" + to.params.loginname).then(function(response) {
console.log(response.data.data);
vm.userinfo = response.data.data;
});

vm.$http
.get("/topic_collect/" + to.params.loginname)
.then(function(response) {
console.log(response.data);
vm.collect_topics = response.data.data;
});
});
},
beforeRouteUpdate: function(to, from, next) {
var app = this;
this.$http.get("/user/" + to.params.loginname).then(function(response) {

app.userinfo = response.data.data;
});

app.$http.get("/topic_collect/" + to.params.loginname)
.then(function(response) {

app.collect_topic = response.data.data;
});
next();
}
};



