export default {
name:'center',
//获取登录名
computed:{
loginname:function(){
    return this.$store.state.loginname;
}
},
created:function(){
    //跳转到用户界面  所有编程式路由跳转
    //   this.$router.push,这个方法会像history添加一个记录，点击后退返回到上一个页面
    //上一个页面的记录不存在  【代替】
    this.$router.replace('/user/'+this.loginname);
}
}



